.willow {
  position: relative;
  filter: grayscale(0.5) contrast(0.95) brightness(0.9);
}


.willow img {
  width: 100%;
  z-index: 1;
}


.willow:before {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
