}


.willow::after {
  background-color: #d8cdcb;
  mix-blend-mode: color;
}

* {
  box-sizing: border-box;
  outline: none;
  -webkit-overflow-scrolling: touch;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

ul {
  list-style: none;
}

ul li {
  display: inline-block;
}

body, html {
  background: white;
  color: #333;
  font-family: 'Lazurski-Regular', 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  background: #090909;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ico-check {
  width: 1em;
  height: 1em;
  fill: initial;
}

.ico-i-move {
  width: 1em;
  height: 1em;
  fill: #010002;
}

.ico-i-share {
  width: 1em;
  height: 1em;
  fill: initial;
}

.ico-i-user {
  width: 1em;
  height: 1em;
  fill: #a7a9ac;
}

.ico-upload {
  width: 1em;
  height: 1em;
  fill: initial;
}

button {
  display: inline-block;
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
}

button:focus {
  outline: none;
}

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  font-size: 1.4rem;
  transition: all 0.25s ease-in-out;
}

.btn:focus, .btn:active {
  outline: none;
}

.btn--block {
  display: block;
  width: 100%;
}

.btn-line span {
  position: relative;
  z-index: 2;
}

.btn-line::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  left: 50%;
  right: 50%;
  background: currentColor;
  height: 2px;
  transition: all 0.25s ease-in-out;
}

.btn-line.is-active::after {
  left: -5%;
  right: -5%;
}

.btn-submit {
  background: white;
  padding: 7px 35px;
  border-radius: 6px;
}

.hasHover .btn-line:hover::after {
  left: -5%;
  right: -5%;
}

.icon {
  display: inline-block;
  font-style: normal;
  content: ' ';
}

.ico {
  fill: currentColor;
}

.intro {
  position: relative;
  z-index: 1;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  padding: 50px 40px 50px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
}

.intro__bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  transform: scale(1.6);
  opacity: 1;
  min-height: 100%;
}

.intro__wrapper {
  position: relative;
  z-index: 2;
  flex: 1 0 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.intro__logo {
  max-width: 200px;
  margin-bottom: 40px;
  opacity: 0;
  transform: translate3d(0, 20px, 0);
}

.intro__logo img {
  max-width: 100%;
}

.intro__description {
  display: flex;
  max-width: 800px;
  margin: 0 auto;
  font-family: 'Open Sans', sans-serif;
}

.intro__control {
  width: 33.3%;
  flex: 0 1 33.3%;
  padding: 20px;
  text-align: center;
  font-size: 16px;
  line-height: 1.7;
  opacity: 0;
  transform: translate3d(0, 20px, 0);
}

.intro__control .ico {
  color: rgba(255, 255, 255, 0.6);
  width: 100%;
  display: inline-block;
  font-size: 35px;
  margin: 20px auto 0 auto;
}

.intro__cta {
  position: absolute;
  bottom: 5vh;
  left: 50%;
  z-index: 2;
  transform: translate3d(-50%, -35px, 0);
  opacity: 0;
}

@media only screen and (max-width: 992px) {
  .intro__control {
    font-size: 14px;
  }
  .intro__control .ico {
    margin-top: 15px;
    font-size: 30px;
  }
}

@media only screen and (max-width: 768px) {
  .intro__logo {
    max-width: 150px;
  }
  .intro__description {
    flex-direction: column;
  }
  .intro__control {
    padding: 10px 20px;
    flex: 0 0 auto;
    width: 100%;
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
  }
  .intro__cta {
    position: static;
    margin-top: 40px;
    width: 100%;
    text-align: center;
    transform: translate3d(0%, -35px, 0);
  }
}

@media only screen and (max-width: 568px) {
  .intro {
    padding: 50px 20px 50px 20px;
  }
}

@media only screen and (max-width: 320px) {
  .intro__logo {
    max-width: 130px;
    margin-bottom: 20px;
  }
  .intro__control {
    font-size: 13px;
    max-width: 220px;
  }
  .intro__control .ico {
    font-size: 25px;
  }
}

.grid {
  position: relative;
}

.grid-container {
  cursor: url("../../public/tap.png"), auto;
  font-size: 0;
}

.grid-container.is-grabed {
  cursor: url("../../public/grab.png"), auto;
}

.grid-transition {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0vw;
  height: 0vw;
  background: #090909;
  border-radius: 50%;
  pointer-events: none;
  transition: all 0.5s ease;
}

.grid-transition.active {
  width: 150vw;
  height: 150vw;
}

.grid-filter {
  position: fixed;
  top: 50px;
  left: 30px;
  z-index: 11;
  display: flex;
  align-items: center;
  color: white;
  transition: transform .3s ease;
}

.grid-filter span {
  flex: 0 0 auto;
  font-size: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  display: inline-block;
  margin-right: 40px;
  padding: 10px;
}

.grid-filter span.is-active:after, .grid-filter span:hover:after {
  left: 5px;
  right: 5px;
}

.grid-filter span:last-child {
  margin-right: 0;
}

@media only screen and (max-width: 992px) {
  .grid-filter span {
    margin-right: 20px;
  }
}

@media only screen and (max-width: 768px) {
  .grid-filter span {
    margin-right: 10px;
  }
}

@media only screen and (max-width: 568px) {
  .grid-filter {
    top: 41px;
    left: 10px;
  }
  .grid-filter span {
    font-size: 15px;
  }
  .grid-filter span:last-child {
    margin-right: 0;
  }
}

@media only screen and (max-width: 460px) {
  .grid-filter {
    top: 0px;
    transform: rotate(90deg);
    float: left;
    transform-origin: left bottom 0;
  }
}

@media only screen and (max-width: 414px) and (max-height: 600px) {
  .grid-filter {
    right: 20px;
  }
  .grid-filter span {
    margin-right: 20px;
    letter-spacing: 0;
    font-weight: 400;
  }
}

