.profile-info__content p a::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  z-index: -1;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  left: 50%;
  right: 50%;
  background: currentColor;
  height: 2px;
  transition: all 0.25s ease-in-out;
}

.profile-info__content p a.is-active::after, .profile-info__content p a:hover::after {
  left: -5%;
  right: -5%;
}

.profile-info .videodetector, .profile-info__video {
  position: relative;
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
}

.profile-info .videodetector iframe, .profile-info__video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.profile-info .videodetector input, .profile-info__video input {
  display: none;
}

.profile-info .remove-videodetector {
  display: none;
}

.profile-info__cta {
  margin-top: 30px;
  margin-left: 50px;
}

.profile-info__cta .btn {
  font-size: 16px;
  text-transform: uppercase;
}

.profile-info__next, .profile-info__prev {
  position: fixed;
  color: white;
  cursor: pointer;
}

.profile-info__next .btn, .profile-info__prev .btn {
  font-size: 16px;
  text-transform: uppercase;
}

.profile-info__next {
  right: 30px;
  top: calc(50% + 80px);
  transform-origin: right bottom 0;
  transform: rotate(90deg);
}

.profile-info__prev {
  left: 10px;
  top: calc(50% - 40px);
  transform-origin: left bottom 0;
  transform: rotate(90deg);
}

.profile .profile-animation {
  opacity: 0;
  transform: translateY(20%);
}

@media only screen and (max-width: 768px) {
  .profile__wrapper {
    flex-direction: column;
    min-height: 1px;
    max-height: auto;
    height: auto;
  }
  .profile-image {
    flex: 0 0 auto;
    margin-top: 50px;
    width: 100%;
    height: auto;
  }
  .profile-info {
    flex: 0 0 auto;
    width: 100%;
  }
  .profile-info__name {
    font-size: 50px;
    line-height: 1.1;
    margin: -28px 0px 20px -0px;
  }
  .profile-info__position {
    margin: 15px 0px 0px 40px;
    font-size: 16px;
  }
  .profile-info__content {
    flex: 0 0 auto;
    overflow-y: hidden;
    margin-left: 0px;
    margin-bottom: 50px;
    max-width: 100%;
  }
  .profile-info__content p {
    font-size: 16px;
  }
  .profile-info__cta {
    margin-left: 0;
  }
}

@media only screen and (max-width: 568px) {
  .profile__wrapper {
    position: relative;
    padding: 80px 20px 20px 20px;
  }
  .profile-info__next, .profile-info__prev {
    font-size: 15px;
  }
  .profile-info__next {
    position: absolute;
    right: 30px;
    top: 100px;
    transform-origin: right bottom 0;
    transform: none;
  }
  .profile-info__prev {
    position: absolute;
    left: 20px;
    top: 100px;
    transform-origin: left bottom 0;
    transform: none;
  }
  .profile-image {
    margin-left: -20px;
    margin-right: -20px;
    width: calc(100% + 40px);
  }
}

.topbar {
  position: absolute;
  top: 30px;
  right: 40px;
  left: 40px;
  min-height: 78px;
  z-index: 10;
  color: white;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  opacity: 0;
  pointer-events: none;
  transition: all .3s .3s ease;
}

.topbar__nav {
  margin-right: 59px;
  font-size: 16px;
}

.topbar__back-grid {
  opacity: 0;
  margin-right: auto;
  transition: all .3s ease;
}

.topbar__logo {
  opacity: 0;
  pointer-events: none;
  flex: 0 0 auto;
  max-width: 140px;
  margin-right: auto;
}

.topbar__logo img {
  max-width: 100%;
}

.topbar__share {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 40px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  pointer-events: none;
}

.topbar__share-el {
  font-size: 18px;
  margin-bottom: 15px;
  opacity: 0;
  transform: translate3d(0, 15px, 0);
  transition: all 0.25s ease-in-out;
}

.topbar__share-el:nth-child(2) {
  transition-delay: .15s;
}

.topbar__share-el:nth-child(3) {
  transition-delay: .25s;
}

.topbar__share-el:nth-child(4) {
  transition-delay: .35s;
}

.topbar__share.is-active {
  pointer-events: all;
}

.topbar__share.is-active .topbar__share-el {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.grid .topbar, .s-page .topbar, .profile .topbar, .submit .topbar {
  opacity: 1;
  pointer-events: all;
}

.profile .topbar__back-grid, .s-page .topbar__back-grid, .profile .topbar__back-grid, .submit .topbar__back-grid {
  opacity: 1;
}

@media only screen and (max-width: 992px) {
  .topbar__nav {
    margin-right: 30px;
  }
}

@media only screen and (max-width: 768px) {
  .topbar__nav {
    display: none;
    margin-right: 20px;
  }
  .topbar__nav.topbar__back-grid {
    display: inline-block;
  }
}

@media only screen and (max-width: 568px) {
  .topbar {
    top: 20px;
    right: 20px;
    left: 20px;
  }
}

@media only screen and (max-width: 350px) {
  .topbar__logo {
    max-width: 130px;
  }
}

.menu {
  position: fixed 0;
  z-index: -1;
  display: flex;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  text-transform: uppercase;
  transition: opacity .3s ease;
}

.menu.is-active {
  opacity: 1;
  pointer-events: all;
}

.menu__wrapper {
  position: relative;
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  padding: 100px 20px 20px 20px;
}

.menu__navigation {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow-y: auto;
}

.menu__li {
  margin-bottom: 6vh;
  transition: all .3s ease;
  opacity: 0;
  transform: translate3d(0, 40px, 0);
  animation-duration: .3s;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
}

.menu__li:nth-child(1) {
  animation-delay: .2s;
}

.menu__li:nth-child(2) {
  animation-delay: .4s;
}

.menu__li:nth-child(3) {
  animation-delay: .6s;
}

.menu__li:nth-child(4) {
  animation-delay: .8s;
}

.menu__li:nth-child(5) {
  animation-delay: 1s;
}

.menu__li:nth-child(6) {
  animation-delay: 1.2s;
}

.menu.is-active .menu__li {
  animation-name: revealMenu;
}

@keyframes revealMenu {
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.page-loading {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1000;
  transform: translate(-50%, -50%);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.preloader-name {
  margin-top: 15px;
  text-align: center;
  font-size: 16px;
  opacity: .6;
}

.preloader-squares {
  width: 50px;
  height: 50px;
  font-size: 0;
}

.preloader-squares .square {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 10px;
  background: #666;
  opacity: 0.3;
  animation: movein 2s infinite;
}

.preloader-squares .square:nth-child(1) {
  transform: translate(0, -25px);
}

.preloader-squares .square:nth-child(2) {
  transform: translate(25px, 0);
}

.preloader-squares .square:nth-child(3) {
  transform: translate(-25px, 0);
}

.preloader-squares .square:nth-child(4) {
  transform: translate(0, 25px);
}

@keyframes movein {
  33% {
    transform: translate(0, 0);
    opacity: 0.7;
  }
  66% {
    transform: translate(0, 0);
    opacity: 0.7;
  }
}

.s-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
}

.s-page__content {
  margin: auto;
  width: 100%;
  max-width: 800px;
  padding: 100px 40px 40px 40px;
  display: flex;
  flex-direction: column;
  font-family: 'Lazurski-Regular';
  transition: all .3s ease;
}

.s-page__content p {
  font-size: 20px;
  line-height: 1.7;
}

.s-page__content code, .s-page__content kbd, .s-page__content samp {
  font-style: normal;
  font-family: 'Lazurski-Regular';
}

.s-page__title {
  font-size: 36px;
  margin: .7em 0;
}

@media only screen and (max-width: 768px) {
  .s-page__content p {
    font-size: 17px;
  }
  .s-page__title {
    font-size: 32px;
  }
}

@media only screen and (max-width: 568px) {
  .s-page__content {
    padding: 80px 20px 40px 20px;
  }
}

@media only screen and (max-width: 374px) {
  .s-page__content p {
    font-size: 15px;
  }
  .s-page__title {
    font-size: 28px;
  }
}

.submit {
  max-width: 1240px;
  margin: 0 auto;
}

.submit__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 100px 40px 20px 40px;
  color: white;
  min-height: 100vh;
}

.submit__wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.submit__image {
  position: relative;
  flex: 0 0 400px;
  max-width: 400px;
}

.submit__image-text {
  margin-top: 20px;
  margin-bottom: 30px;
  font-size: 18px;
  line-height: 1.5;
  text-align: center;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.submit__image-preview {
  display: block;
  position: relative;
  overflow: hidden;
  height: 300px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.submit__image-preview:hover {
  border-color: white;
  background: rgba(255, 255, 255, 0.05);
}

.submit__image-preview img {
  width: 100%;
  height: 100%;
  display: block;
  margin: 0 auto;
  object-fit: cover;
  opacity: 0;
  transition: opacity 0.25s ease-in-out;
}

.submit__image-preview.is-chosen img {
  opacity: 1;
}

.submit__image-preview.is-chosen .submit__image-placeholder {
  opacity: 0;
}

.submit__image-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  text-align: center;
  transition: opacity 0.25s ease-in-out;
}

.submit__image-placeholder .ico {
  font-size: 50px;
}

.submit__image-placeholder span {
  margin-top: 10px;
  display: block;
  text-align: center;
  font-size: 20px;
}

.submit__image-file-input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  width: 0.1em;
  height: 0.1em;
}

.submit__inner {
  flex: 0 1 auto;
  width: 100%;
  padding: 0 0px 0 40px;
}

.submit__inner-btn {
  margin-top: 30px;
  margin-bottom: 20px;
  text-align: center;
}

.submit__inner-btn .btn {
  background: transparent;
  color: white;
}

.form-group {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);
  font-family: 'Open Sans', 'Roboto', sans-serif;
}

.form-group input, .form-group textarea {
  display: block;
  width: 100%;
  color: white;
  border-radius: 0px;
  padding: 10px;
  background: none;
  border: 1px solid #6D6875;
  resize: vertical;
  transition: all 0.25s ease-in-out;
}

.form-group input:focus, .form-group textarea:focus {
  border: 1px dotted #fda737;
  color: #FFF;
  background: #000;
}

.form-group input {
  max-width: 350px;
}

@media only screen and (max-width: 992px) {
  .submit__wrapper {
    flex-direction: column;
  }
  .submit__image {
    margin-top: 70px;
    flex: 0 0 auto;
    width: 100%;
  }
  .submit__image-preview {
    height: 220px;
  }
  .submit__inner {
    flex: 0 0 auto;
    width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
  }
}
