// buttons
button {
  display: inline-block;
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
  &:focus {
    outline: none; } }

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  font-size: 1.4rem;
  transition: all $transitionSpeed ease-in-out;
  &:focus, &:active {
    outline: none; }
  &--block {
    display: block;
    width: 100%; }
  &-line {
    span {
      position: relative;
      z-index: 2; }
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      z-index: 1;
      top: 50%;
      transform: translate3d(0,-50%,0);
      left: 50%;
      right: 50%;
      background: currentColor;
      height: 2px;
      transition: all $transitionSpeed $ease; }
    &.is-active {
      &::after {
        left: -5%;
        right: -5%; } } }

  &-submit {
    background: white;
    padding: 7px 35px;
    border-radius: 6px;

    // reverse animation
    // &.is-active
    //   &:hover
    //     &::after
    //       left: 50%
 } }    //       right: 50%

.hasHover .btn-line:hover {
  &::after {
    left: -5%;
    right: -5%; } }
