
.grid {
  position: relative;
  &-container {
    // cursor: grabbing
    cursor: url('../../public/tap.png'), auto;
    font-size: 0;
    &.is-grabed {
      cursor: url('../../public/grab.png'), auto; } }

  &-transition {
    position: absolute;
    top: 50%;
    left: 50%;
    // transform: translate(-50%, -50%) scale(0)
    transform: translate(-50%, -50%);
    width: 0vw;
    height: 0vw;
    background: #090909;
    border-radius: 50%;
    pointer-events: none;
    transition: all 0.5s ease;
    &.active {
      width: 150vw;
      height: 150vw;
 } }      // transform: translate(-50%, -50%) scale(20)

  &-filter {
    position: fixed;
    top: 50px;
    left: 30px;
    // bottom: 20px
    // right: 55px
    z-index: 11;
    display: flex;
    align-items: center;
    // transform: rotate(90deg)
    // float: right
    // transform-origin: right top 0
    color: white;
    transition: transform .3s ease;
    span {
      flex: 0 0 auto;
      font-size: 16px;
      // font-family: 'Open Sans', sans-serif
      // font-weight: 600
      letter-spacing: 0.1em;
      text-transform: uppercase;
      display: inline-block;
      margin-right: 40px;
      padding: 10px;
      &.is-active, &:hover {
        &:after {
          left: 5px;
          right: 5px; } }
      &:last-child {
        margin-right: 0; } } } }

@include r($desktop) {
  .grid {
    &-filter {
      span {
        margin-right: 20px; } } } }
@include r($tablet) {
  .grid {
    &-filter {
      span {
        margin-right: 10px; } } } }

@include r($mobile) {
  .grid {
    &-filter {
      top: 41px;
      left: 10px;
      span {
        font-size: 15px;
        &:last-child {
          margin-right: 0; } } } } }

@include r(460) {
  .grid {
    &-filter {
      top: 0px;
      transform: rotate(90deg);
      float: left;
      transform-origin: left bottom 0; } } }

// +rh(600)
//   .grid-filter
//     transform: rotate(0)

@media only screen and (max-width: 414px) and (max-height: 600px) {
  .grid {
    &-filter {
      right: 20px;
      span {
        margin-right: 20px;
        letter-spacing: 0;
        font-weight: 400; } } } }
