.s-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
  &__content {
    margin: auto;
    width: 100%;
    max-width: 800px;
    padding: 100px 40px 40px 40px;
    display: flex;
    flex-direction: column;
    font-family: 'Lazurski-Regular';
    transition: all .3s ease;
    p {
      font-size: 20px;
      line-height: 1.7; }
    code, kbd, samp {
      font-style: normal;
      font-family: 'Lazurski-Regular'; } }
  &__title {
    font-size: 36px;
    margin: .7em 0; } }

@include r($tablet) {
  .s-page {
    &__content {
      p {
        font-size: 17px; } }
    &__title {
      font-size: 32px; } } }

@include r($mobile) {
  .s-page {
    &__content {
      padding: 80px 20px 40px 20px; } } }

@include r(374) {
  .s-page {
    &__content {
      p {
        font-size: 15px; } }
    &__title {
      font-size: 28px; } } }
