.topbar {
  position: absolute;
  top: 30px;
  right: 40px;
  left: 40px;
  min-height: 78px;
  z-index: 10;
  color: white;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  opacity: 0;
  // transform: translate3d(50px,0px,0)
  pointer-events: none;
  // text-transform: uppercase
  transition: all .3s .3s ease;
  &__nav {
    margin-right: 59px;
    font-size: 16px; }
  &__back-grid {
    opacity: 0;
    margin-right: auto;
    transition: all .3s ease; }

  &__logo {
    opacity: 0;
    pointer-events: none;
    flex: 0 0 auto;
    max-width: 140px;
    margin-right: auto;
    img {
      max-width: 100%; } }

  &__share {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    pointer-events: none;
    &-el {
      font-size: 18px;
      margin-bottom: 15px;
      opacity: 0;
      transform: translate3d(0,15px,0);
      transition: all $transitionSpeed $ease;
      &:nth-child(2) {
        transition-delay: .15s; }
      &:nth-child(3) {
        transition-delay: .25s; }
      &:nth-child(4) {
        transition-delay: .35s; } }
    &.is-active {
      pointer-events: all;
      .topbar__share-el {
        opacity: 1;
        transform: translate3d(0,0,0); } } } }


.grid, .s-page, .profile, .submit {
  .topbar {
    opacity: 1;
    // transform: translate3d(0,0,0)
    pointer-events: all; } }

.profile, .s-page, .profile, .submit {
  .topbar {
    &__back-grid {
      opacity: 1; } } }

@include r($desktop) {
  .topbar {
    &__nav {
      margin-right: 30px; } } }

@include r($tablet) {
  .topbar {
    &__nav {
      display: none;
      margin-right: 20px;
      &.topbar__back-grid {
        display: inline-block; } } } }

@include r($mobile) {
  .topbar {
    top: 20px;
    right: 20px;
    left: 20px; } }

@include r(350) {
  .topbar {
    &__logo {
      max-width: 130px; } } }

.menu {
  position: fixed 0;
  z-index: -1;
  display: flex;
  flex-direction: column;
  background: rgba(black, .8);
  opacity: 0;
  pointer-events: none;
  text-transform: uppercase;
  transition: opacity .3s ease;
  &.is-active {
    opacity: 1;
    pointer-events: all; }

  &__wrapper {
    position: relative;
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    padding: 100px 20px 20px 20px; }

  &__navigation {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    overflow-y: auto; }
  &__li {
    // flex: 1 0 auto
    margin-bottom: 6vh;
    transition: all .3s ease;
    opacity: 0;
    transform: translate3d(0,40px,0);
    animation-duration: .3s;
    animation-timing-function: ease-out;
    animation-fill-mode: both;
    &:nth-child(1) {
      animation-delay: .2s; }
    &:nth-child(2) {
      animation-delay: .4s; }
    &:nth-child(3) {
      animation-delay: .6s; }
    &:nth-child(4) {
      animation-delay: .8s; }
    &:nth-child(5) {
      animation-delay: 1s; }
    &:nth-child(6) {
      animation-delay: 1.2s; } } }

// animation on active element
.menu.is-active {
  .menu {
    &__li {
      animation-name: revealMenu; } } }

@keyframes revealMenu {
  to {
    opacity: 1;
    transform: translate3d(0,0,0); } }

@include r($mobile) {
  .menu {} }

.page-loading {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1000;
  transform: translate(-50%,-50%);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.preloader-name {
  margin-top: 15px;
  text-align: center;
  font-size: 16px;
  opacity: .6; }

.preloader-squares {
  width: 50px;
  height: 50px;
  font-size: 0;
  .square {
    display: inline-block;
    width: 15px;
    height: 15px;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 10px;
    background: #666;
    opacity: 0.3;
    animation: movein 2s infinite;
    &:nth-child(1) {
      transform: translate(0, -25px); }
    &:nth-child(2) {
      transform: translate(25px, 0); }
    &:nth-child(3) {
      transform: translate(-25px, 0); }
    &:nth-child(4) {
      transform: translate(0, 25px); } } }

@keyframes movein {
  33% {
    transform: translate(0, 0);
    opacity: 0.7; }

  66% {
    transform: translate(0, 0);
    opacity: 0.7; } }
